<template>
	<view>
		<view class="cu-custom" :style="[{height:CustomBar + 'px'}]">
			<!-- <image src="https://div.buy315.com.cn/xcx_imgs/content_top.png" ></image> -->
			<view class="cu-bar fixed" :style="style" :class="[bgImage!=''?'none-bg text-white bg-img':'',bgColor]">
				<view class="action">
					<text  @tap="BackPage" v-if="isBack" class="cuIcon-back"></text>
					<slot  @tap="BackPage" v-if="isBack" name="backText"></slot>
				</view>
				<view class="content" :style="[{top:StatusBar + 'px'}]" >
					<slot name="content"></slot>
				</view>
				<view class="" style="padding-right: 20rpx; font-size: 30rpx;" @click="rightcli">
					<slot name="right"></slot>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				StatusBar: this.StatusBar,
				CustomBar: this.CustomBar,
				bgImage:''
			};
		},
		name: 'sundeheng-custom',
		computed: {
			style() {
				var StatusBar= this.StatusBar;
				var CustomBar= this.CustomBar;
				var bgImage = this.bgImage;
				var style = `height:${CustomBar}px;padding-top:${StatusBar}px;`;
				if (this.bgImage) {
					style = `${style}background-image:url(${bgImage});`;
				}
				return style
			}
		},
		props: {
			bgColor: {
				type: String,
				default: ''
			},
			isBack: {
				type: [Boolean, String],
				default: false
			},
			bgImage: {
				type: String,
				default: ''
			},
		},
		methods: {
			BackPage() {
				uni.navigateBack({
					delta: 1
				});
			},
			rightcli(){
				this.$emit('rightcli')
			}
		}
	}
</script>

<style>
	@import "main.css";
	
	@font-face {
		font-family: "cuIcon";

		src: url(https://div.buy315.com.cn/xcx_imgs/content_top.png)format('svg');
