			/* IE6-IE8 */
			url('data:data:image/jpeg;base64,') format('woff'),
			url('//at.alicdn.com/t/font_533566_yfq2d9wdij.ttf?t=1545239985831') format('truetype'),
			/* chrome, firefox, opera, Safari, Android, iOS 4.2+*/
			url('') format('svg');
		/* iOS 4.1- */
	}
	[class*="cuIcon-"] {
		font-family: "cuIcon";
		font-size: inherit;
		font-style: normal;
	}
	
	.cuIcon-back:before {
		content: "\e679";
	}
</style>
